<template>
  <div class="goods-edit">
    <div class="input-item">
      <em>商品名称</em>
      <p>芒果蛋糕</p>
    </div>
    <div class="input-item">
      <em>价格</em>
      <p>￥120.0起</p>
    </div>
    <div class="input-item">
      <em>当前库存</em>
      <cube-input v-model="sum" :maxlength="len" type="number" style="width: 100px;" />
      <cube-button :outline="true" :inline="true">置满</cube-button>
    </div>
    <div class="input-item">
      <em>最大库存</em>
      <span>999</span>
    </div>
    <div class="input-item">
      <em>库存次日置满</em>
      <cube-switch v-model="flag" />
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      flag: true,
      sum: 122,
      len: 5
    }
  }
}
</script>

<style lang="stylus" scoped>
.goods-edit {
  padding: 50px 20px;
  .input-item {
    display: flex;
    padding: 5px 0;
    line-height: 40px;
    border-bottom: 1px solid #ccc;
    em {
      flex: 1;
    }
  }
}
</style>




